<template>
  <div id="coursedetail-studentlist">
    <Card class="noradius-card mytheme-noborder mytheme-card-padding mytheme-content-minheight" dis-hover>
      <!-- -->
      <Table
        v-if="isHasEnded"
        class="student-table mytheme-table noborder-table tbody-min-height"
        :stripe="true"
        :disabled-hover="true"
        :columns="hasEndedColData"
        :data="readTabData"
      >
      </Table>
      <!-- -->
      <Table
        v-else
        class="mytheme-mg-top-30 student-table mytheme-table noborder-table tbody-min-height"
        :stripe="true"
        :disabled-hover="true"
        :columns="colData"
        :data="readTabData"
      >
      </Table>
      <pagination
        class="mytheme-mg-top-30"
        :total="total"
        :display="display"
        :currentPage="current"
        @pageChange="pageChange"
      >
      </pagination>
    </Card>
    <!--  -->
    <Modal class="mytheme-modal" v-model="isShowModal" width="800">
      <div class="modal-head" slot="header"></div>
      <div class="modal-body">
        <Table class="mytheme-table noborder-table" :columns="readColData" :data="readTabData"></Table>
        <pagination
          class="clearfloat mytheme-mg-top-30 mytheme-mg-bottom-30"
          v-if="readTabData.length > 0"
          :total="readTabPage.total"
          :display="readTabPage.display"
          :current-page="readTabPage.current"
        ></pagination>
      </div>
      <div class="modal-foot" slot="footer">
        <i-button class="modal-foot-btn " type="primary" @click="review(1)"></i-button>
        <i-button class="modal-foot-btn mytheme-btn" type="default" @click="isShowModal = false"></i-button>
      </div>
    </Modal>

    <!---->
    <Modal class="mytheme-modal" v-model="isShowRatingModal" :closable="false">
      <div class="modal-head" slot="header"></div>
      <div class="modal-body">
        <div class="body-content">
          <div class="content-title">{{$route.query.periodname}}</div>
          <div class="content-starttime">{{$route.query.starttime}}</div>
          <div class="content-student">
            <div class="inline-block">{{ratingModal.studentName}}</div>
            <div class="inline-block mytheme-mg-left-70">
              <Rate v-model="ratingModal.score"></Rate>
            </div>
          </div>
          <div class="content-rating">
            <div></div>
            <Form ref="formValidate" :model="ratingModal" :rules="ruleValidate">
              <FormItem prop="comment">
                <Input type="textarea" :rows="6" :maxlength="200" v-model.trim="ratingModal.comment"/>
                <div class="color-hui fr font-size-12" v-if="ratingModal.comment && ratingModal.comment.length">
                  {{ratingModal.comment.length}}/200
                </div>
                <div class="color-hui fr font-size-12" v-else>0/200</div>
              </FormItem>
            </Form>
          </div>
        </div>
      </div>
      <div class="modal-foot mytheme-mg-bottom-30" slot="footer">
        <i-button class="modal-foot-btn" type="primary" @click="handleSubmit('formValidate');"></i-button>
        <i-button class="modal-foot-btn mytheme-btn" type="default" @click="isShowRatingModal = false"></i-button>
      </div>
    </Modal>
  </div>
</template>
<script>
  import pagination from "@/components/pagination";
  import Server from "@/service/request";
  import Path from '@/service/Path';

  import {myDate, ERR_OK} from '../utils';
  import city from '@/Api/city';
  import {Table, Modal, Button, Form, FormItem, Rate, Input, Card,} from "iview";

  export default {
    name: "coursedetail-studentlist",
    props: ["data"],
    components: {
      Table,
      pagination,
      Modal,
      Button,
      Form,
      FormItem,
      Rate,
      Input,
      Card,
    },
    data() {
      return {
        colData: [
          {title: "", key: "id", align: 'center'},
          {title: "", key: "nickname", align: 'center'},
          {
            title: "", align: 'center',
            render: (h, params) => {
              function renderSex(param) {
                if (param == 1) {
                  return '';
                } else if (parma == 2) {
                  return '';
                } else {
                  return '';
                }
              }

              return h('div', params.row.sex == 1 ? '' : '');
            }
          },
          {title: "", key: "mobile", align: 'center'},
          {title: "", key: "age", align: 'center'},
          {
            title: "", align: 'center',
            render: (h, params) => {
              function renderCountry(param) {
                if (param) {
                  return city[param];
                } else {
                  return '----';
                }
              }

              return h('div', [
                h('span', {
                  style: {
                    marginRight: (params.row.province && params.row.city) ? '10px' : '0',
                  }
                }, renderCountry(params.row.country)),
                h('span', {
                  style: {
                    marginRight: '10px',
                    display: params.row.province ? 'ineline-block' : 'none',
                  }
                }, city[params.row.province]),
                h('span', {
                  style: {
                    display: params.row.city ? 'ineline-block' : 'none',
                  },
                }, city[params.row.city]),
              ]);
            }
          },
          {
            title: "",
            align: 'center',
            render: (h, params) => {
              function renderProfile(param) {
                if (param == '') {
                  return '...';
                }
              }

              return h('div', params.row.profile || renderProfile(params.row.profile));
            }
          }
        ],
        hasEndedColData: [
          {
            title: "", align: 'center',
            render: (h, params) => {
              return h('div', params.index + 1)
            }
          },
          {title: "", key: "nickname", align: 'center'},
          {
            title: "", align: 'center',
            render: (h, params) => {
              return h('div', params.row.attendancestatus == 1 ? '' : '');
            }
          },
          {
            title: "", align: 'center',
            render: (h, params) => {
              /**
               * 
               * @param param 
               * @returns {string} 
               */
              function renderBtn(param) {
                return (param == '' || param == null) ? '' : param;
              }

              return h('div', {
                attrs: {
                  class: params.row.comment ? '' : 'color-orange',
                },
                style: {
                  cursor: params.row.comment ? 'normal' : 'pointer'
                },
                on: {
                  'click': () => {
                    if (params.row.comment != '' && params.row.comment != null) {
                      return;
                    }
                    this.isShowRatingModal = true;
                    this.tableIndex = params.index;
                    this.ratingModal.studentName = params.row.nickname;
                    this.ratingModal.score = params.row.score;
                    this.ratingModal.comment = params.row.comment;
                  }
                },
              }, renderBtn(params.row.comment))
            }
          },
          {
            title: '',
            key: 'score',
            align: 'center'
          },
        ],
        readColData: [
          {
            title: "", align: 'center',
            render: (h, params) => {
              return h('div', params.index + 1)
            },
            width: 80,
          },
          {title: "", key: "nickname", align: 'center'},
          {
            title: "", align: 'center',
            render: (h, params) => {
              return h('i-switch', {
                props: {
                  value: params.row.attendancestatus == 1,
                },
                on: {
                  'on-change': (event) => {
                    this.readTabData[params.index].attendancestatus = event ? '1' : '0';
                  }
                }
              });
            },
          },
          // {
          //   title: "", align: 'center',
          //   render: (h, params) => {
          //     return h('div', [
          //       h('i-rate', {
          //         props: {
          //           allowHalf: false,
          //           value: Number(params.row.score) || 1,
          //         },
          //         on: {
          //           'on-change': (event) => {
          //             this.readTabData[params.index].score = event;
          //           }
          //         },
          //       }),
          //     ]);
          //   }
          // },
          // {
          //   title: "", align: 'center',
          //   render: (h, params) => {
          //     return h('div', [
          //       h('i-input', {
          //         props: {
          //           value: params.row.comment,
          //         },
          //         on: {
          //           'on-blur': (event) => {
          //             this.readTabData[params.index].comment = event.target.value;
          //             this.ratingModal.score = params.row.score;
          //             this.ratingModal.comment = params.row.comment;
          //             this.ratingModal.studentName = params.row.nickname;
          //           }
          //         },
          //       }),
          //     ]);
          //   }
          // },
        ],
        readTabData: [],
        tabData: [],  //
        tempData: [], //
        display: 10,
        total: 0,
        current: 1,
        readTabPage: {
          display: 10,
          total: 0,
          current: 1,
        },
        isHasEnded: false, //
        isShowModal: false,
        isShowRatingModal: false,
        isShowReadTab: false,
        studentSum: 0,
        startTime: this.$route.query.starttime,
        endTime: this.$route.query.endtime,
        tableIndex: '',
        ratingModal: {
          studentName: '',
          score: 0,
          comment: '',
        },

        ruleValidate: {
          comment: [
            {required: true, message: '', trigger: 'blur'}
          ],
        },
      };
    },
    created() {
      this.getOverStudentList(this.current);
      if (!myDate.getNowTime(this.$route.query.endtime)) {
        this.getStudentList(this.current);
      }
    },
    watch: {
      isShowRatingModal(val) {
        if (!val) {
          this.tableIndex = '';
        }
      }
    },
    methods: {
      /**
       * 
       * @param num 
       */
      getStudentList(num) {
        Server.post(Path.periodStulist, {
          'toteachtimeid': this.$route.query.toteachtimeid,
          'pagenum': num
        }, (res) => {
          if (res.code === ERR_OK) {
            let arr = [], data = res.data.data;
            if (this.$Utils.isJson(res.data.data)) {
              for (let k in data) {
                arr.push(data[k]);
              }
              this.readTabData = arr;
            } else if (this.$Utils.isArray(data)) {
              this.readTabData = res.data.data;
            }
            this.studentSum = res.data.sum;
            this.readTabPage.total = res.data.pageinfo.total;
            this.readTabData.forEach(el => {
              this.$set(el, 'comment', '');
              this.$set(el, 'score', 0);
              this.$set(el, 'attendancestatus', 1);
            })
          }
        });
      },
      /**
       * 
       */
      handleSubmit (name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            this.review(2);
          }
        })
      },
      /**
       * ,
       * @param pagenum 
       */
      getOverStudentList(pagenum) {
        Server.post(Path.getAttendance, {
          'lessonsid': this.$route.query.lessonsid, 'pagenum': pagenum
        }, (res) => {
          if (res.code === ERR_OK) {
            if (myDate.getNowTime(this.$route.query.endtime)) {//
              this.isHasEnded = true;
              if (res.data.allstatus === 0) {//
                this.isShowModal = true;
                this.getStudentList(this.current);
              } else {//
                this.isShowModal = false;
              }
              this.readTabData = res.data.data.data;           //
              this.studentSum = res.data.data.pageinfo.total;  //
              this.total = res.data.data.pageinfo.total;
            } else {//
              this.isHasEnded = false;
              this.getStudentList(this.current);
            }
          }
        });
      },
      // attendancestatus 
      //
      pageChange(current) {
        this.getOverStudentList(current);
      },
      /**
       * 
       * @param type 1  2
       */
      review(type) {
        let arr = [];
        let subArr = [];
        this.readTabData.forEach((el, i) => {
          if(type === 1) {
            arr.push({
              lessonsid: this.$route.query.lessonsid,
              studentid: el.studentid || el.id,
              attendancestatus: el.attendancestatus,
              // score: el.score || 0,
              // comment: el.comment,
            });
          } else if (type === 2) {
            arr.push({
              lessonsid: this.$route.query.lessonsid,
              studentid: el.studentid || el.id,
              //attendancestatus: el.attendancestatus,
              score: el.score,
              comment: el.comment,
            });
          }

        });
        //
        if (this.tableIndex !== '') {
          arr[this.tableIndex].comment = this.ratingModal.comment;
          arr[this.tableIndex].score = this.ratingModal.score;
        }
        if(type === 2){
          if(!this.ratingModal.score){
            this.$Message.error('')
            return false
          }
        }
        Server.post(Path.upAttendance, arr, res => {
          let {code, info} = res;
          this.isShowModal = false;
          this.isShowRatingModal = false;
          if (code === ERR_OK) {
            this.$Message.success(info);
            this.$emit('addWareSuccess');
            this.getOverStudentList(this.current);
          } else {
            this.$Message.error(info);
          }
        });
      },
    },
  };
</script>
<style lang="less" scoped>
  @import "../assets/courseinfo-title.less";

  .student-table {
    margin-top: 44px;
    max-width: 1396px;
  }

  .content-title {
    font-size: 16px;
    font-weight: bold;
    color: #333333;
  }

  .content-starttime,
  .content-student,
  .content-rating {
    line-height: 40px;
    font-size: 14px;
    color: #333333;
  }

  .inline-block {
    display: inline-block;
  }
</style>



