<template>
  <div id="order-details">
    <panel-title :crumbs="crumbs"></panel-title>
    <div class="mytheme-container-mg-30">
      <Card dis-hover>
        <p slot="title">{{$t('Main.MyOrders.OrderInfo.CardTitle')}}</p>
        <!---->
        <div class="callout callout-info" v-if="isHasOrderInfo">
          <!--  -->
          <div class="callout-item">
            <label style="">{{$t('Main.MyOrders.OrderInfo.OrderNum')}}{{$t('Main.Symbols.Colon')}}</label>
            <span>{{buyer.ordernum}}</span>
          </div>
          <!--  -->
          <div class="callout-item">
            <label style="">{{$t('Main.MyOrders.OrderInfo.OrderStatus')}}{{$t('Main.Symbols.Colon')}}</label>
            <span>{{checkOrderStatus(buyer.orderstatus)}}</span>
          </div>
          <!--  -->
          <div class="callout-item">
            <label style="">{{$t('Main.MyOrders.OrderInfo.OrderTime')}}{{$t('Main.Symbols.Colon')}}</label>
            <span>{{buyer.ordertime}}</span>
          </div>
          <!--  -->
          <div class="callout-item">
            <label style="">{{$t('Main.MyOrders.OrderInfo.Student')}}{{$t('Main.Symbols.Colon')}}</label>
            <span>{{buyer.studentname}}</span>
          </div>
          <!--  -->
          <div class="callout-item">
            <label style="">{{$t('Main.MyOrders.OrderInfo.OrderSource')}}{{$t('Main.Symbols.Colon')}}</label>
            <span>{{checkordersource(buyer.ordersource)}}</span>
          </div>
          <!--  -->
          <div class="callout-item">
            <label style="">{{$t('Main.MyOrders.OrderInfo.PayMent')}}{{$t('Main.Symbols.Colon')}}</label>
            <span>{{checkPaytype(buyer.paytype)}}</span>
          </div>
        </div>
        <!---->
        <div class="callout callout-warning" v-if="isHasOrderInfo">
          <div class="callout-item">
            <label style="">{{$t('Main.MyOrders.OrderInfo.OldTotalPrice')}}{{$t('Main.Symbols.Colon')}}</label>
            <span>{{$t('Main.Symbols.CurrencySymbol')}}{{seller.originprice}}</span>
          </div>
          <div class="callout-item">
            <label style="">{{$t('Main.MyOrders.OrderInfo.DiscountAmount')}}{{$t('Main.Symbols.Colon')}}</label>
            <span>{{$t('Main.Symbols.CurrencySymbol')}}{{seller.discount}}</span>
          </div>
          <div class="callout-item">
            <label style="">{{$t('Main.MyOrders.OrderInfo.DealAmount')}}{{$t('Main.Symbols.Colon')}}</label>
            <span>{{$t('Main.Symbols.CurrencySymbol')}}{{seller.amount}}</span>
          </div>
          <div class="callout-item">
            <label style="">{{$t('Main.MyOrders.OrderInfo.CourseName')}}{{$t('Main.Symbols.Colon')}}</label>
            <span>{{seller.coursename}}</span>
          </div>
          <div class="callout-item">
            <label style="">{{$t('Main.MyOrders.OrderInfo.Teacher')}}{{$t('Main.Symbols.Colon')}}</label>
            <span>{{seller.nickname}}</span>
          </div>
        </div>
        <!---->
        <defaultShow v-else :text="$t('Main.noData')"></defaultShow>
      </Card>
    </div>
  </div>
</template>

<script>
  import {Card, Button, Form, FormItem, Col, Row} from 'iview'
  import panelTitle from '@/components/panelTitle'
  import Server from '@/service/request'
  import requestPath from '@/service/Path'
  import defaultShow from '@/components/defaultShow';

  export default {
    name: "order-details",
    components: {
      Card,
      Button,
      Form,
      FormItem,
      panelTitle,
      Col,
      Row,
      Server,
      requestPath,
      defaultShow
    },
    data() {
      return {
        crumbs: [
          {name: this.$t('Main.MyOrders.AllOrders'), route: '/MyOrders'}, //
          {name: this.$t('Main.MyOrders.OrderInfo.OrderMessage'), route: ''}, //
        ],
        buyer: {
          ordernum: '', //
          orderstatus: '', //
          ordertime: '', // 
          studentname: '', //
          ordersource: '', //
          paytype: '', //
        },
        seller: {
          originprice: '', //
          discount: '', //
          amount: '', //
          coursename: '', //
          classname: '', //
          nickname: '', // 
        },
        refund: {},
        isFinishe: false,
        isRefund: false,
        isHasOrderInfo: false,
        message: ''
      }
    },
    created() {
      if (this.$Utils.isLogin()) {
        this.crumbs[1].route = this.$route.fullPath;
        this.getOrderInfo();
      } else {
        this.$router.replace('/Login');
        return;
      }
    },
    methods: {
      // 
      getOrderInfo() {
        Server.post(requestPath.order_orderInfo, {
          orderid: this.$route.query.orderid
        }, (res) => {

          if (res.code === 0) {
            // 
            this.buyer.ordernum = res.data.ordernum;
            this.buyer.ordertime = res.data.ordertime;
            this.buyer.paytype = res.data.paytype;
            this.buyer.ordersource = res.data.ordersource;
            this.buyer.studentname = res.data.studentname;
            this.buyer.orderstatus = res.data.orderstatus;
            // 
            this.seller.originprice = res.data.originprice;
            this.seller.discount = res.data.discount;
            this.seller.amount = res.data.amount;
            this.seller.coursename = res.data.coursename;
            this.seller.classname = res.data.classname;
            this.seller.nickname = res.data.nickname;
            this.isHasOrderInfo = true;
          } else {
            this.isHasOrderInfo = false;
            this.message = this.$t('Main.MyOrders.OrderInfo.Message');
          }
        }).catch(err => {
          throw err;
        });
      },
      /**
       * 
       * @param  {[Number]} val 1web 2
       * @return {[String]}     
       */
      checkordersource(val) {
        if (val === 1) {
          return this.$t('Main.MyOrders.OrderInfo.WebPlatform');
        } else if (val === 2) {
          return this.$t('Main.MyOrders.OrderInfo.MobilePlatform');
        }
      },
      /**
       * 
       * @param  {[Number]} val 1 2 3 4 0
       * @return {[String]}     
       */
      checkPaytype(val) {
        if (val === 1) {
          return this.$t('Main.MyStudent.Balance')
        } else if (val === 0) {
          return this.$t('Main.MyStudent.Other');
        } else if (val === 2) {
          return this.$t('Main.MyStudent.Wx');
        } else if (val === 3) {
          return this.$t('Main.MyStudent.Alipay');
        } else if (val === 4) {
          return this.$t('Main.MyStudent.Unionpay');
        }
      },
      /**
       * 
       * @param  {[Number]} val 0 10 20 30
       * @return {[String]}     
       */
      checkOrderStatus(val) {
        if (val === 0) {
          return this.$t('Main.MyStudent.HaveOrder');
        } else if (val === 10) {
          return this.$t('Main.MyStudent.HasBeenCancelled');
        } else if (val === 20) {
          return this.$t('Main.MyStudent.HaveToPay');
        } else if (val === 30) {
          return this.$t('Main.MyStudent.ApplayRefund');
        }
      },
    },
  }
</script>

<style scoped>
  .callout {
    padding: 20px 0 20px 60px;
    margin: 20px 40px;
    border: 1px solid #eee;
    border-left-width: 5px;
    border-radius: 4px;
  }

  .callout-item {
    line-height: 2;
  }

  .callout-item label {
    display: inline-block;
    text-align: right;
    width: 100px;
    font-size: 14px;
  }

  .callout-item span {
    padding: 0 0 0 30px;
    font-size: 14px;
  }

  .callout,
  .callout-default {
    border-left-color: #999;
    background: #eee;
  }

  .callout-info {
    border-left-color: #469af8;
    background: #f2f7fb;
  }

  .callout-warning {
    border-left-color: #f89240;
    background: #fffbf8;
  }

  .callout-danger {
    border-left-color: #f5496d;
    background: #f2dbe0;
  }

  .callout-primary {
    border-left-color: #cae3ff;
    background: #fff;
  }

  .callout-success {
    border-left-color: #3adfa7;
    background: #f7fffc;
  }
</style>



