<template>
  <div id="task-base">
    <Form class="mytheme-form" :label-width="100" inline>
      <FormItem label="">
        <span v-if="sum!==''">{{sum}}</span>
      </FormItem>
    </Form>
    <Form class="mytheme-form" :label-width="100" inline>
      <FormItem class="mg-right" label="">
        <Select
          class="input-max-width input-max-height"
          v-model="selectSchedulingid"
          :disabled="isDisable"
          @on-change="$emit('getLessonid', selectSchedulingid), updateLessonId()">
          <Option :value="el.keys" v-for="(el, i) in schedulingidArr" :key="el.key">{{el.value}}</Option>
        </Select>
      </FormItem>
      <FormItem class="mg-right" label="">
        <Select
          class="input-max-width input-max-height"
          :disabled="isDisable"
          v-model="selectLessonId"
        >
          <Option :value="el.value.id||''" v-for="(el, i) in lessidArr" @click.native="emitEvent(el)" :key="i">{{el.value.periodname}}</Option>
        </Select>
      </FormItem>
      <FormItem label="">
        <DatePicker
          class="input-max-height"
          style="width: 320px;"
          type="datetimerange"
          placeholder=" - "
          v-model="selectDate"
          :options="options1"
          :editable="false"
          format="yyyy-MM-dd HH:mm:ss"
          @on-change="$emit('selectDate', selectDate)"
        >
        </DatePicker>
      </FormItem>
    </Form>
  </div>
</template>
<script>
  import {Form, FormItem, Input, Select, Option, DatePicker, Row, Col, Button} from 'iview';

  export default {
    name: "task-base",
    props: ['schedulingidArr', 'lessidArr', 'sum'],
    components: {
      Form, FormItem, Input, Select, Option, DatePicker, Row, Col, Button
    },
    data() {
      return {
        options1: {
          disabledDate(date) {
            return date && date.valueOf() < Date.now() - 86400000;
          },
        },
        selectSchedulingid: 0,
        selectLessonId: 0,
        selectPeriodid: 0,
        selectDate: '',
        isDisable: false,
      }
    },
    watch: {
      selectSchedulingid() {
        this.selectLessonId = 0;
      },
    },
    methods: {
      updateLessonId() {
        this.$emit('emptyLessonId');
      },
      emitEvent(e) {
        this.selectLessonId = e.value.id;
        this.selectPeriodid = e.value.periodid;
        this.$emit('getHomeWorkContent', this.selectSchedulingid, this.selectLessonId, this.selectPeriodid);
      },
      /**
       * id
       */
      routeFrom() {
        let route = this.$route, query = route.query;
        if ((query && query.schedulingid) && (query.lessonid)) {
          this.initSelectVal();
        }
      },
      /**
       * 
       */
      initSelectVal() {
        let route = this.$route, query = route.query;
        this.isDisable = true;
        this.schedulingidArr.forEach(el => {
          //
          if(el.keys == query.schedulingid) {
            this.selectSchedulingid = el.keys;//
            this.$emit('getLessonid', el.keys);//
          }
        });
      },
      /**
       *
       */
      initSelectLesson() {
        this.lessidArr.forEach(el => {
          //
          if(el.value.id == this.$route.query.lessonid) {
            this.selectLessonId = el.value.id||'';//
            this.selectPeriodid = el.keys;//id
            this.$emit('getHomeWorkContent', this.selectSchedulingid, this.selectLessonId, this.selectPeriodid);//
          }
        });
      },
    },
  }
</script>
<style lang="less" scoped>
  #task-base {
    margin-top: 34px;
    .mg-right {
      margin-right: 70px;
    }
  }
</style>



